.page {
  font-size: 2.3529411765vw;
  line-height: 2.3529411765vw;
  overflow-x: hidden;
}
@media (min-width: 769px) {
  .page {
    font-size: 0.6944444444vw;
    line-height: 0.6944444444vw;
  }
}
.page::before {
  content: "";
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #000;
  z-index: -1;
  opacity: 0;
  transition: opacity 0.3s ease-in-out;
}
.page--hidden {
  overflow: hidden;
}
.page--hidden::before {
  opacity: 0.3;
  z-index: 1;
}

.page__body {
  margin: 0;
  background-color: #fff;
  font-family: "e-Ukraine", "Arial", sans-seriv;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  padding-top: 8.5rem;
}

.page__footer {
  margin-top: auto;
}

@media screen and (min-width: 769px) {
  .page__aside {
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
  }
}
button {
  font: inherit;
  background-color: transparent;
  border: none;
  padding: 0;
}

input {
  padding: 0;
  border: none;
  font: inherit;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "e-Ukraine Head", "e-Ukraine", "Arial", sans-seriv;
}

.visually-hidden {
  position: absolute;
  width: 0.0694444444vw;
  height: 0.0694444444vw;
  margin: -0.0694444444vw;
  padding: 0;
  border: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
  visibility: hidden;
}

.header {
  background-color: #fff;
  font-family: "e-Ukraine Head", "e-Ukraine", "Arial", sans-seriv;
  position: fixed;
  width: 100%;
  z-index: 99;
  top: 0;
  left: 0;
  transition: 0.3s ease-in-out;
}
.header--hidden {
  transform: translateY(-100%);
}

.header__wrapper {
  padding: 2rem 1.5rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  align-content: center;
}
@media screen and (min-width: 769px) {
  .header__wrapper {
    padding: 3rem;
  }
}

.header__logo {
  font-family: "e-Ukraine Head", "e-Ukraine", "Arial", sans-seriv;
  font-weight: 600;
  font-size: 2rem;
  line-height: 110%;
  width: min-content;
  text-align: center;
}
.header__logo img {
  display: none;
}

.header__burger {
  position: relative;
  width: 2.2rem;
  height: 1.2rem;
}
.header__burger::before, .header__burger::after {
  position: absolute;
  content: "";
  display: block;
  width: 100%;
  height: 0.2rem;
  left: 0;
  background-color: #000;
  border-radius: 0.1rem;
  transition: all 0.2s cubic-bezier(0.1, 0.82, 0.76, 0.965);
}
.header__burger::before {
  top: 0;
}
.header__burger::after {
  bottom: 0;
}
.header__burger:hover {
  color: #262626;
}
.header__burger--active::before {
  transform: rotate(45deg);
  top: 0.5rem;
}
.header__burger--active::after {
  transform: rotate(-45deg);
  bottom: 0.5rem;
}
@media screen and (min-width: 769px) {
  .header__burger {
    display: none;
  }
}

.header__list {
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin: 0;
  padding: 2rem 0;
  list-style-type: none;
  right: 0;
  bottom: 0;
  width: 100%;
  transform: translateY(-100%);
  background-color: #fff;
  transition: 0.4s ease-in-out;
}
@media screen and (min-width: 769px) {
  .header__list {
    position: relative;
    flex-direction: row;
    transform: unset;
    column-gap: 1.5rem;
    width: max-content;
  }
}
.header__list--active {
  transform: translateY(99%);
}

.header__item {
  position: relative;
  overflow: hidden;
  padding: 1rem 2rem;
}
.header__item::before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 0;
  height: 0.1rem;
  background-color: #ffd700;
  transition: 0.5s ease-in-out;
}
@media screen and (min-width: 768px) {
  .header__item {
    padding: 0 0.5rem 0.5rem;
  }
  .header__item:hover::before {
    width: 100%;
  }
  .header__item:hover .header__link {
    color: #ffd700;
  }
}

.header__link {
  text-decoration: none;
  color: #000;
  font-size: 2rem;
  line-height: 2.5rem;
  transition: 0.5s ease-in-out;
}
@media screen and (min-width: 769px) {
  .header__link {
    font-size: 1.8rem;
    line-height: 2rem;
  }
}

.footer {
  background-color: rgba(0, 0, 0, 0.7);
  padding: 2rem 2rem;
}
@media screen and (min-width: 768px) {
  .footer {
    padding: 2rem 17rem;
  }
}

.footer__copyright {
  margin-top: 2.5rem;
  color: rgba(255, 255, 255, 0.7);
}

.block {
  margin: 5rem 2rem;
}
@media screen and (min-width: 768px) {
  .block--documents {
    background-color: #067bff;
    border-radius: 4rem;
    color: #fff;
  }
}
@media screen and (min-width: 768px) and (min-width: 768px) {
  .block--documents .block__item {
    background-color: #fff;
    color: #0057b8;
    transition: 0.3s ease-in-out;
  }
  .block--documents .block__item:hover {
    background-color: #0057b8;
    color: #fff;
  }
}
@media screen and (min-width: 768px) {
  .block--projects .block__wrapper {
    gap: 4rem 2rem;
  }
}
@media screen and (min-width: 768px) {
  .block--donate .block__wrapper {
    display: flex;
    flex-direction: column;
    row-gap: 4rem;
  }
}
@media screen and (min-width: 769px) {
  .block {
    display: flex;
    flex-wrap: wrap;
  }
}

.block__title {
  font-size: 2rem;
  line-height: 2.3rem;
  text-transform: capitalize;
  text-align: center;
  margin: 2rem auto 3rem;
  padding: 1.3rem 1.5rem;
  border-radius: 2.5rem;
  backdrop-filter: blur(5px);
  width: 100%;
  text-transform: uppercase;
  position: relative;
}
.block__title::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 5rem;
  height: 0.1rem;
  background-color: #0057b8;
}

.block__button {
  display: block;
  text-decoration: none;
  width: max-content;
  height: min-content;
  padding: 2.2rem 2.5rem;
  border: 0.1rem solid #0057b8;
  color: #0057b8;
  font-size: 2rem;
  font-weight: 700;
  font-family: "e-Ukraine Head", "e-Ukraine", "Arial", sans-seriv;
  border-radius: 3rem;
  margin: 0 auto;
  transition: 0.3s ease-in-out;
}
.block__button:hover {
  background-color: #0057b8;
  color: #fff;
}

.block__wrapper {
  display: flex;
  flex-direction: column;
  row-gap: 2rem;
  width: 100%;
  padding: 4rem 0;
}
@media screen and (min-width: 768px) {
  .block__wrapper {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem;
    padding: 4rem 15rem;
  }
}

.block__text {
  font-size: 1.2rem;
  line-height: 1.7rem;
}
@media screen and (min-width: 769px) {
  .block__text {
    width: 52%;
  }
}

.block__photo {
  display: block;
  border-radius: 5rem;
  overflow: hidden;
  margin-left: auto;
}
@media screen and (min-width: 768px) {
  .block__photo {
    width: 45%;
  }
}
.block__photo img {
  object-fit: cover;
  width: 100%;
  height: auto;
}

.block__item {
  display: block;
  padding: 4rem 2rem;
  text-decoration: none;
  font-size: 1.8rem;
  line-height: 2rem;
  background-color: #0057b8;
  color: #fff;
  border-radius: 3rem;
}

.history {
  position: relative;
  display: flex;
  flex-direction: column;
}

.history__item {
  position: relative;
}
@media screen and (min-width: 768px) {
  .history__item {
    display: grid;
    grid-template-columns: 30% 1fr;
    column-gap: 5%;
  }
}
.history__item:not(:first-of-type) {
  border-top: 0.1rem solid #0057b8;
}
@media screen and (min-width: 768px) {
  .history__item:not(:first-of-type) .history__text::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 1rem;
    height: 1rem;
    background-color: #0057b8;
    border-radius: 50%;
    transform: translate(-50%, -50%);
  }
}

.history__date {
  position: relative;
  margin: 0;
  font-weight: 700;
  font-size: 2rem;
  line-height: 100%;
  padding: 2.5rem 0;
}

.history__text {
  position: relative;
  font-size: 1.8rem;
  line-height: 110%;
  padding: 2.5rem 1.5rem;
}
@media screen and (min-width: 768px) {
  .history__text {
    border-left: 0.1rem solid #0057b8;
  }
}
.history__text h1,
.history__text h2,
.history__text h3,
.history__text h4,
.history__text h5,
.history__text h6,
.history__text h7,
.history__text p,
.history__text ol,
.history__text ul {
  margin-top: 0;
}

.item {
  position: relative;
  display: flex;
  flex-direction: column;
  row-gap: 0.8rem;
  color: #000;
  padding: 1rem 2rem;
  border-radius: 2rem;
  overflow: hidden;
  background-color: transparent;
}

.item__title {
  font-size: 2rem;
  margin: 0;
}

.item__photo {
  width: 100%;
  height: auto;
}

.item__desc {
  font-size: 1.5rem;
  line-height: 1.7rem;
  max-height: 5.1rem;
  margin: 0 0 1rem;
}

.item__link {
  color: #000;
  text-decoration: none;
  font-size: 1.8rem;
  padding-bottom: 0.4rem;
  position: relative;
}
.item__link::before {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  width: 0;
  height: 0.1rem;
  background-color: #000;
  transition: 0.3s ease-in-out;
}
.item__link:hover::before {
  width: 100%;
}

.requisites {
  padding: 0;
  background-color: transparent;
  color: #0057b8;
  position: relative;
  width: max-content;
}

.requisites__title {
  width: max-content;
  margin: 0 0 2rem;
  line-height: 2.4rem;
}

.requisites__icon {
  width: 2.4rem;
  height: 2.4rem;
}

.requisites__check {
  display: flex;
  cursor: pointer;
  column-gap: 1rem;
  align-items: center;
  transition: 0.35s ease-in-out;
}
.requisites__check:hover {
  color: #0057b8;
}

.social__list {
  display: flex;
  column-gap: 2rem;
  list-style-type: none;
  padding: 0;
  margin: 0;
}

.social__link {
  text-decoration: none;
  font-size: 2rem;
  color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 0.1rem solid #fff;
  border-radius: 50%;
  width: 3rem;
  height: 3rem;
  transition: 0.2s ease-in-out;
}
.social__link:hover {
  background-color: #fff;
}
.social__link:hover .social__icon {
  fill: #000;
}

.social__icon {
  width: 1.5rem;
  height: 1.5rem;
  fill: #fff;
  transition: 0.3s ease-in-out;
}

.tooltip {
  position: absolute;
  top: 0;
  right: 0;
  transform: translate(100%, -30%);
  font-size: 1.5rem;
  font-weight: 200;
  opacity: 0;
  user-select: none;
  transition: 0.3s ease-in-out;
}
.tooltip--visible {
  opacity: 1;
}